
import { defineComponent } from "vue";

export default defineComponent({
  name: "pagination",

  props: {
    totalPage: { type: Number, required: true },
    modelValue: {
      type: Number,
      default: 1,
    },
  },

  setup(_, { emit }) {
    const changeCurrentPage = (numberPage) => {
      emit("update:modelValue", numberPage);
    };

    return { changeCurrentPage };
  },
});
